@use '../base/mixins' as m;
.header{

 
    position: absolute;

    width: 100%;
    z-index: 10000;
    animation: aparecer 1s ease ;

    @include m.tablet{
      
        height: 120px;
  
        

    }

    &.visible{

        visibility: visible !important;
    }

 
   
}

.menu{
visibility: visible;
    width: 30px;
    margin: 1rem 0;
    position: absolute;
    top: 2%;
    left: 2%;
    @include m.tablet{
        visibility: hidden;
    }
}


.container-nav{
    display: flex;
    visibility: hidden;
    padding: 1rem 0;
    height: 100%;
    margin: 0;
    background-color: rgba(0, 12, 13, 0.731);
    flex-direction: column;
    align-items: center;
    justify-content: center;
    @include m.tablet{
        background-color: transparent;
        visibility: visible;

        flex-direction: row;
        align-items: center;
        margin: 0 2rem;
        justify-content: space-between;
    }

    .titulo{
  
            display: flex;
            align-items: center;
        


       color: white;
        font-weight: 900;
        img{
            margin: 0 5px;
            width: 35px;
        }
        h1{
            line-height: -10px;
        }
    }
    .span-lightblue{
        color: rgb(4, 172, 227);
    }
 
    .navegacion{
       
    
            display: flex;
            align-items: center;
         
        .search{
            background-color: transparent;
            border: none;
            border-bottom: 1px solid white;
            outline: none;
            width: 100px;
            color: white;
            &:focus{
                width: 150px;
                transition: width .5s ease-in-out;
            }
        }

    }
}


///BTN MENU
.menu{
    cursor: pointer;
}

.logo-lupa{
    height: 30px;
    display: flex;
    align-items: center;
    button{
        border-radius: 30px;
        height: 100%;
        &:hover{
            cursor: pointer;
            background-color: rgba(0, 0, 0, 0.554);
            transition: background .2s ease-in;
        }
        background-color: transparent;
        border: none;
        color: white;
    }
}


.visible{
    visibility: visible !important;
    z-index: 1000;
    height: 100%;
}


