.spinning{
    position: relative;
    background-color: #ffffffe9;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;

}

.spin{
   display: grid;
   place-items: center;
    background-color: transparent;
    width: 50px;
    height: 50px;
    border: 2px solid rgb(255, 208, 0);
    border-radius: 30px;
    border-left: none;
    animation: girarSpin .5s linear infinite;
 
}


@keyframes girarSpin {
    from{
        rotate: 0deg;
    }
    to{
        rotate: 360deg;
    }
}