html {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  overflow-y: hidden;
  font-family: "Oswald", sans-serif;
}
body.no-fijo {
  overflow-y: scroll !important;
}

.contenedor {
  max-width: 1200px;
  margin: 0 auto;
}

.no-visible {
  display: none !important;
}

img {
  display: block;
  width: 100%;
  height: 100%;
}

picture {
  height: 100%;
}

@keyframes aparecer {
  0%, 90% {
    -webkit-filter: blur(0px);
    -moz-filter: blur(0px);
    -o-filter: blur(0px);
    -ms-filter: blur(0px);
  }
  50% {
    -webkit-filter: blur(50px);
    -moz-filter: blur(50px);
    -o-filter: blur(50px);
    -ms-filter: blur(50px);
  }
}
html {
  line-height: 1.15; /* 1 */
  -webkit-text-size-adjust: 100%; /* 2 */
}

/* Sections
   ========================================================================== */
/**
 * Remove the margin in all browsers.
 */
body {
  margin: 0;
}

/**
 * Render the `main` element consistently in IE.
 */
main {
  display: block;
}

/**
 * Correct the font size and margin on `h1` elements within `section` and
 * `article` contexts in Chrome, Firefox, and Safari.
 */
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

/* Grouping content
   ========================================================================== */
/**
 * 1. Add the correct box sizing in Firefox.
 * 2. Show the overflow in Edge and IE.
 */
hr {
  box-sizing: content-box; /* 1 */
  height: 0; /* 1 */
  overflow: visible; /* 2 */
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */
pre {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/* Text-level semantics
   ========================================================================== */
/**
 * Remove the gray background on active links in IE 10.
 */
a {
  background-color: transparent;
}

/**
 * 1. Remove the bottom border in Chrome 57-
 * 2. Add the correct text decoration in Chrome, Edge, IE, Opera, and Safari.
 */
abbr[title] {
  border-bottom: none; /* 1 */
  text-decoration: underline; /* 2 */
  text-decoration: underline dotted; /* 2 */
}

/**
 * Add the correct font weight in Chrome, Edge, and Safari.
 */
b,
strong {
  font-weight: bolder;
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */
code,
kbd,
samp {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/**
 * Add the correct font size in all browsers.
 */
small {
  font-size: 80%;
}

/**
 * Prevent `sub` and `sup` elements from affecting the line height in
 * all browsers.
 */
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

/* Embedded content
   ========================================================================== */
/**
 * Remove the border on images inside links in IE 10.
 */
img {
  border-style: none;
}

/* Forms
   ========================================================================== */
/**
 * 1. Change the font styles in all browsers.
 * 2. Remove the margin in Firefox and Safari.
 */
button,
input,
optgroup,
select,
textarea {
  font-family: inherit; /* 1 */
  font-size: 100%; /* 1 */
  line-height: 1.15; /* 1 */
  margin: 0; /* 2 */
}

/**
 * Show the overflow in IE.
 * 1. Show the overflow in Edge.
 */
button,
input { /* 1 */
  overflow: visible;
}

/**
 * Remove the inheritance of text transform in Edge, Firefox, and IE.
 * 1. Remove the inheritance of text transform in Firefox.
 */
button,
select { /* 1 */
  text-transform: none;
}

/**
 * Correct the inability to style clickable types in iOS and Safari.
 */
button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

/**
 * Remove the inner border and padding in Firefox.
 */
button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

/**
 * Restore the focus styles unset by the previous rule.
 */
button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

/**
 * Correct the padding in Firefox.
 */
fieldset {
  padding: 0.35em 0.75em 0.625em;
}

/**
 * 1. Correct the text wrapping in Edge and IE.
 * 2. Correct the color inheritance from `fieldset` elements in IE.
 * 3. Remove the padding so developers are not caught out when they zero out
 *    `fieldset` elements in all browsers.
 */
legend {
  box-sizing: border-box; /* 1 */
  color: inherit; /* 2 */
  display: table; /* 1 */
  max-width: 100%; /* 1 */
  padding: 0; /* 3 */
  white-space: normal; /* 1 */
}

/**
 * Add the correct vertical alignment in Chrome, Firefox, and Opera.
 */
progress {
  vertical-align: baseline;
}

/**
 * Remove the default vertical scrollbar in IE 10+.
 */
textarea {
  overflow: auto;
}

/**
 * 1. Add the correct box sizing in IE 10.
 * 2. Remove the padding in IE 10.
 */
[type=checkbox],
[type=radio] {
  box-sizing: border-box; /* 1 */
  padding: 0; /* 2 */
}

/**
 * Correct the cursor style of increment and decrement buttons in Chrome.
 */
[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

/**
 * 1. Correct the odd appearance in Chrome and Safari.
 * 2. Correct the outline style in Safari.
 */
[type=search] {
  -webkit-appearance: textfield; /* 1 */
  outline-offset: -2px; /* 2 */
}

/**
 * Remove the inner padding in Chrome and Safari on macOS.
 */
[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * 1. Correct the inability to style clickable types in iOS and Safari.
 * 2. Change font properties to `inherit` in Safari.
 */
::-webkit-file-upload-button {
  -webkit-appearance: button; /* 1 */
  font: inherit; /* 2 */
}

/* Interactive
   ========================================================================== */
/*
 * Add the correct display in Edge, IE 10+, and Firefox.
 */
details {
  display: block;
}

/*
 * Add the correct display in all browsers.
 */
summary {
  display: list-item;
}

/* Misc
   ========================================================================== */
/**
 * Add the correct display in IE 10+.
 */
template {
  display: none;
}

/**
 * Add the correct display in IE 10.
 */
[hidden] {
  display: none;
}

.header {
  position: absolute;
  width: 100%;
  z-index: 10000;
  animation: aparecer 1s ease;
}
@media (min-width: 768px) {
  .header {
    height: 120px;
  }
}
.header.visible {
  visibility: visible !important;
}

.menu {
  visibility: visible;
  width: 30px;
  margin: 1rem 0;
  position: absolute;
  top: 2%;
  left: 2%;
}
@media (min-width: 768px) {
  .menu {
    visibility: hidden;
  }
}

.container-nav {
  display: flex;
  visibility: hidden;
  padding: 1rem 0;
  height: 100%;
  margin: 0;
  background-color: rgba(0, 12, 13, 0.731);
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .container-nav {
    background-color: transparent;
    visibility: visible;
    flex-direction: row;
    align-items: center;
    margin: 0 2rem;
    justify-content: space-between;
  }
}
.container-nav .titulo {
  display: flex;
  align-items: center;
  color: white;
  font-weight: 900;
}
.container-nav .titulo img {
  margin: 0 5px;
  width: 35px;
}
.container-nav .titulo h1 {
  line-height: -10px;
}
.container-nav .span-lightblue {
  color: rgb(4, 172, 227);
}
.container-nav .navegacion {
  display: flex;
  align-items: center;
}
.container-nav .navegacion .search {
  background-color: transparent;
  border: none;
  border-bottom: 1px solid white;
  outline: none;
  width: 100px;
  color: white;
}
.container-nav .navegacion .search:focus {
  width: 150px;
  transition: width 0.5s ease-in-out;
}

.menu {
  cursor: pointer;
}

.logo-lupa {
  height: 30px;
  display: flex;
  align-items: center;
}
.logo-lupa button {
  border-radius: 30px;
  height: 100%;
  background-color: transparent;
  border: none;
  color: white;
}
.logo-lupa button:hover {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.554);
  transition: background 0.2s ease-in;
}

.visible {
  visibility: visible !important;
  z-index: 1000;
  height: 100%;
}

.spinning {
  position: relative;
  background-color: rgba(255, 255, 255, 0.9137254902);
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.spin {
  display: grid;
  place-items: center;
  background-color: transparent;
  width: 50px;
  height: 50px;
  border: 2px solid rgb(255, 208, 0);
  border-radius: 30px;
  border-left: none;
  animation: girarSpin 0.5s linear infinite;
}

@keyframes girarSpin {
  from {
    rotate: 0deg;
  }
  to {
    rotate: 360deg;
  }
}
.background-image {
  filter: brightness(0.7);
  overflow: hidden;
  height: 100vh;
}
.background-image img {
  object-position: center;
  object-fit: cover;
}

.preview-countrys {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 100;
  display: flex;
  align-items: flex-end;
}

.container-ubicacion-datos {
  margin: 2.5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container-countrys {
  animation: alargar 6s ease;
  backdrop-filter: blur(10px);
  padding: 1rem 0;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.196);
}
.container-countrys .card-country {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  letter-spacing: 3px;
  color: white;
  width: 100%;
  border-radius: 10px;
  padding: 0;
}
@media (min-width: 768px) {
  .container-countrys .card-country {
    padding: 3rem 1rem;
  }
}

.container-pais h2 {
  margin: 10px 0;
  font-size: 60px;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .container-pais h2 {
    font-size: 100px;
  }
}
.container-pais p {
  margin: 0;
}

.container-temperatura {
  margin: 3rem 0;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .container-temperatura {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
  }
}
.container-temperatura p {
  font-size: 40px;
  margin: 0;
  font-weight: 600;
}
@media (min-width: 768px) {
  .container-temperatura p {
    font-size: 60px;
    display: inline;
  }
}
.container-temperatura span {
  margin: 0 1rem;
  text-align: center;
}

.container-clima {
  margin: 0.5rem 0;
  text-transform: uppercase;
}
.container-clima h3 {
  margin: 0;
  font-size: 20px;
}
@media (min-width: 768px) {
  .container-clima h3 {
    font-size: 30px;
  }
}

.container-datos {
  display: flex;
  align-items: center;
  justify-content: center;
}

.container-sensacion p {
  margin: 0.2rem 1rem;
  margin: 0 !important;
  font-size: 15px !important;
}

.container-humedad p, .container-viento p, .container-clouds p {
  font-size: 13px;
}
@media (min-width: 768px) {
  .container-humedad p, .container-viento p, .container-clouds p {
    margin: 0.2rem 1rem;
    font-size: 15px;
    text-align: center;
  }
}

.container-logo-clima {
  text-align: center;
  width: 90px;
  height: 90px;
}
.container-logo-clima img {
  margin: 0 auto;
}

.daily {
  overflow-x: scroll;
  width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .daily {
    overflow-x: hidden;
    overflow-y: hidden;
    width: 100%;
  }
}
.daily .contenedor-daily-day {
  display: flex;
}
.daily .contenedor-daily-day p {
  padding: 0rem 0.5rem;
}
.daily .container-daily-img {
  display: flex;
}
.daily .container-daily-img img {
  margin: 0 1.5rem;
  height: 50px;
  width: 50px;
}
.daily .container-daily-temp {
  display: flex;
  align-items: center;
}
.daily .container-daily-temp p {
  margin: 0 1.3rem;
}

.container-sensacion p {
  margin: 0.2rem 1rem;
  font-size: 18px;
}

@keyframes alargar {
  from {
    width: 0%;
    backdrop-filter: blur(0);
  }
  to {
    width: 100%;
    backdrop-filter: blur(10px);
  }
}
.container-daily-temp-img {
  display: flex;
  flex-direction: column;
  align-items: center;
}