@use '../base/variables' as v;
html{
    box-sizing: border-box;
    margin: 0;
    padding: 0;

}

*, *:before, *:after {
    box-sizing: inherit;
}

body{
    overflow-y: hidden;
    font-family: v.$fuentePrincipal;
    &.no-fijo{
        overflow-y: scroll !important;
    }
}

.contenedor{
    max-width: 1200px;
    margin: 0 auto;
}

//OCULTA ALGO
.no-visible{
    display: none !important;
}

img{
    display: block;
    width: 100%;
    height: 100%;
}
picture{
    height: 100%;
}


@keyframes aparecer{
    0%,
    90% {
      -webkit-filter: blur(0px);
      -moz-filter: blur(0px);
      -o-filter: blur(0px);
      -ms-filter: blur(0px);
    }
    50% {
      -webkit-filter: blur(50px);
      -moz-filter: blur(50px);
      -o-filter: blur(50px);
      -ms-filter: blur(50px);
    }

}