@use '../base/mixins' as m;
.preview-countrys{
   position: absolute;
   width: 100%;
   height:100%;
   z-index: 100;

   display: flex;
   align-items: flex-end;
    
}
.container-ubicacion-datos{
    margin: 2.5rem 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.container-countrys{
  
    animation: alargar 6s ease;
    backdrop-filter: blur(10px);
    padding: 1rem 0;

    width: 100%;
    background-color: rgba(255, 255, 255, 0.196);
   
   

    .card-country{
      
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        letter-spacing: 3px;
       
    color: white;
        width: 100%;
        border-radius: 10px;
padding: 0;
       @include m.tablet{
        padding: 3rem 1rem;
       }
    }
}

//////////////////
/// 
/// 
.container-pais{

    h2{
        margin: 10px 0;
        font-size: 60px;
        text-align: center;
    
        text-transform: uppercase;
        @include m.tablet{
        
            font-size: 100px;
        }
    }
    p{
        margin:0;
    }
}

.container-temperatura{
    margin: 3rem 0;
    display: flex;
    flex-direction: column;
    @include m.tablet{
        display: flex;
        flex-direction: row;
       align-items: flex-end;
        
    }

    p{
      
        font-size: 40px;
        margin: 0;
        font-weight: 600;

        @include m.tablet{
     
            font-size: 60px;
            display: inline;
        }

    }
    span{
        margin: 0 1rem;
        text-align: center;
    }
}

.container-clima{
    margin: .5rem 0;
    text-transform: uppercase;
    h3{
        margin: 0;
    
        font-size: 20px;
        @include m.tablet{
            font-size: 30px;
        }
    }
}

.container-datos{
    display: flex;
    align-items: center;
    justify-content: center;
}

.container-sensacion{
    p{
        margin: .2rem 1rem;
     margin: 0 !important; 
     font-size: 15px !important;  
    }
}

.container-humedad, .container-viento, .container-clouds{

    p{
       

        @include m.tablet{
            margin: .2rem 1rem;
            font-size: 15px ;
            text-align: center;
        }
        font-size: 13px;
    }
 
}


.container-logo-clima{
    text-align: center;
    width: 90px;
    height: 90px;
    img{
        
        margin: 0 auto; 
     
     
    }
}



.daily{
    @include m.tablet{
        overflow-x: hidden;
        overflow-y: hidden;
   
        width: 100%;
    }
    overflow-x: scroll;
    width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    .contenedor-daily-day{
        display: flex;
       p{
        padding: 0rem .5rem;
       }
    }
    .container-daily-img{
        display: flex;
        img{
            margin: 0 1.5rem;
            height: 50px;
            width: 50px;
        }
    }
    .container-daily-temp{
        display: flex;
        align-items: center;
        p{
            margin: 0 1.3rem;
        }
    }
}

.container-sensacion{
    p{

        margin: 0.2rem 1rem;
        font-size: 18px;
    }
}


@keyframes alargar {
    from{
        width: 0%;
        backdrop-filter: blur(0);
    }
    to{
        width: 100%;
        backdrop-filter: blur(10px);
    }
    
}

.container-daily-temp-img{
    display: flex;
    flex-direction: column;
    align-items: center;
}